<template>
  <a-tag v-on="$listeners" :color="iconObject.color" v-bind="$attrs" class="y-tag">
    <a-icon v-if="iconObject.iconType" :type="iconObject.iconType" :theme="iconObject.iconTheme" />
    <slot></slot>
  </a-tag>
</template>

<script>
// 
// 1green
// 2geekblue
// 3red
// 4 goldvolcanogold 

export default {
  name: 'YTag',
  computed: {
    iconObject: {
      get({ $attrs }) {
        return this.statusFilter($attrs);
      },
    },
  },
  methods: {
    statusFilter($attrs) {
      let color = $attrs.color;
      let iconType = null;
      let iconTheme = 'filled';
      if (!$attrs.color && $attrs.status) {
        switch ($attrs.status) {
          case 'waiting':
            iconType = 'clock-circle';
            break;
          case 'success':
            color = 'green';
            iconType = 'check-circle';
            break;
          case 'normal':
            color = 'geekblue';
            iconType = 'loading';
            iconTheme = null;
            break;
          case 'error':
            color = 'red';
            iconType = 'close-circle';
            break;
          case 'warning':
            color = 'gold';
            iconType = 'info-circle';
            break;
          case 'seriousWarning':
            color = 'volcano';
            iconType = 'info-circle';
            break;
        }
      }
      return { color, iconType, iconTheme };
    },
  },
};
</script>
